@import "variables";

.shop-item-card {
  text-align: center;
  border-radius: 10px;
  padding-bottom: 20px;

  .card-text {
    font-weight: bold;
    margin-top: 1rem;
  }

  .btn-secondary {
    @include btn-primary-custom;
  }

  .card-footer {
    background-color: $white;

    .add-to-cart-col {
      text-align: right;
    }

    .price-col {
      text-align: left;
      font-weight: bold;
      font-size: 20px;
    }
  }
}

.landing-page{
  background-image: radial-gradient(circle, #5c0067 0%, #00d4ff 100%);

  .shopping-cart-button {
    background: transparent;
    border: none;
    .badge {
      position: absolute;
      font-size: 20px;
      top: 10%;
      color: $primary;
      background-color: $white;
      border-radius: 50%;
      margin-left: -40px;
    }
  }
}

.checkout-div {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #1c1c25;
  height: 100vh;
}
  .checkout-div::before,
  .card-checkout::before {
    content: '';
    position: absolute;
    width: 100px;
    height: 100px;
    background: linear-gradient(#f00,#f0f);
    border-radius: 50%;
  }

  .checkout-div::before {
    bottom: 15%;
    right: 70%;
  }

  .card-checkout::before {
    margin-top: -95px;
    top: 10%;
    left: 100%;
    background: linear-gradient(rgb(0,152,240), rgb(4,87,211));
  }

  .face {
    width: 750px;
    height: 500px;
    border-radius: 20px;
    box-shadow: 0 15px 35px rgba(0,0,0,0.5);
    background: rgba(255,255,255,0.05);
    filter: blur(0.2px);
    border-width: 0.1px;
    border-style: solid;
    border-color: rgba(255,255,255,0.425);
  }
  .card-checkout {
    background-color: transparent !important;
  }
