

// Brand colors
$primary: #002A4B;
$primary-dark: darken($primary, 10%);
$primary-medium: lighten($primary, 20%);
$primary-light: lighten($primary, 30%);
$secondary: #DC823D;
$secondary-dark: darken($secondary, 10%);
$secondary-light: lighten($secondary, 30%);

$gray-100: #f8f9fa;
$gray-150: #f3f3f3;
$gray-200: #e9ecef;
$gray-300: #dee2e6;
$gray-400: #ced4da;
$gray-500: #adb5bd;
$gray-600: #6c757d;
$gray-900: #212529;
$white: #fff;
$light-gray: $gray-100;
$medium-gray: $gray-500;
$dark-gray: $gray-900;
$black: #000;

@mixin btn-primary-custom {
  background-color: $primary;
  border-color: $primary;
  border-radius: 30px;
  font-weight: 300;
  font-size: 1.5rem;
  color: $white;
  box-shadow: 0 0.1rem 0.2rem rgba(0, 0, 0, .05);

  &:hover:enabled {
    box-shadow: 0 0.3rem 0.4rem rgba(0, 0, 0, .1);
  }

  &:disabled {
    filter: grayscale(1);
    cursor: default;
  }

  &:focus {
    outline: none;
    box-shadow: none;
  }

}

@mixin btn-secondary-custom {
  background-color: $white;
  border-color: $primary;
  color: $primary;
  box-shadow: 0 0.1rem 0.2rem rgba(0, 0, 0, .05);
  border-radius: 30px;

  &:hover:enabled {
    background-color: $primary-light;
    color: $white;
    box-shadow: 0 0.3rem 0.4rem rgba(0, 0, 0, .1);
  }

  &:disabled {
    filter: grayscale(1);
  }

  &:focus {
    outline: none;
    box-shadow: none;
  }
}